<template>
      <v-row style="height:100%;">
          <v-col cols="7" style="height:100%;">
              <v-card height="100%" flat class="ml-sm-10 mr-sm-10">

                 <v-sheet height="7%" width="100%" class="d-flex">
                            <img src="@/assets/pleca-gob1.png" height="100%">
                            <img src="@/assets/pleca-gob2.png" height="100%">
                            <img src="@/assets/pleca_tecnm.jpg" height="100%">
                            <img src="@/assets/pleca_cenidet.jpg" height="100%">

                  </v-sheet>

                  <v-sheet height="15%" width="100%" class="d-flex align-center">
                      <h2 class="cenidet--text">Administrador    </h2>
                  </v-sheet>

                  <v-sheet height="75%" width="60%" class="d-flex justify-center align-center" elevation="3">

                    <v-sheet height="80%" width="85%">
                        <v-sheet height="90%" width="100%">
                            <v-form
                              height="10%"
                              width="100%"
                               ref="formadministrador"
                              >
                              <v-text-field
                                label="Correo"
                                required
                                v-model="data.email"
                                :rules="rules.email"
                              ></v-text-field>
                                                      <br>

                              <v-text-field
                                label="Contraseña"
                                required
                                v-model="data.password"
                                :rules="rules.requerido"
                              ></v-text-field>
                              <v-alert type="error" dense v-if="alert">
                                Datos incorrectos
                              </v-alert>
                              <br>
                              <v-sheet class="d-flex align-center">

                                <v-btn color="primary" link  @click="onsubmit">
                                  Aceptar
                                </v-btn>
                              </v-sheet>
                            </v-form>
                        </v-sheet>
                    </v-sheet>

                  </v-sheet>

              </v-card>
          </v-col>

          <v-col cols="5" style="height:100%;">
              <v-sheet height="100%" class="d-flex justify-end">
                      <img src="@/assets/escudo.png" height="100%" style="transform: scaleX(-1);">
              </v-sheet>
          </v-col>
      </v-row>
</template>

<script>
import {  mapActions  } from 'vuex'

export default {
  name: 'login',
  data: () => ({
    alert: false,
    data: {
      path: '/administrador',
      email: '',
      password: ''
    },
    rules: {
        requerido: [v => !!v || 'Campo obligatorio'],
        email: [v => /.+@.+\..+/.test(v) || 'Email invalido'],
    }

  }),
  methods: {
    ...mapActions(['dologin']),
    async onsubmit(){

      if(this.$refs.formadministrador.validate()){

        let response=await this.dologin(this.data)
        if(response){
          this.$router.push('/administrador')
        }else{
          this.alert = true
          setTimeout(() => {
            this.alert = false
          }, 2000);
        }

      }

    }

  }
}
</script>